
.indicator-and-level{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
#sort, #search {
  margin-bottom: 10px;
}
.list-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.list-group-item {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 100%;
          flex: 1 0 100%;
  border-radius: 5px;
  height:80px;
  overflow: hidden;
  margin: 4px;
}
.glyphicon {
  font-size:1.15em;
  color: gray;
  opacity: 0.25;
  margin-right:4px;
  margin-top:.5em;
}
.glyphicon-circle-arrow-up {
  color: green;
  opacity: 0.5;
}
.glyphicon-circle-arrow-down {
  color: red;
  opacity: 0.5;
}
.glyphicon-record {
  color: yellowgreen;
  opacity: 0.5;
}
#sort .glyphicon {
  margin-left: 4px;
  font-size: .8em;
}
#sort .active {
  opacity: 1;
}
.a_overflow,
.b_secure,
.c_observation,
.d_adjustment,
.e_control,
.f_off,
.g_none { 
  height: 100%;
  top: 0; 
  width: 12px; 
  display: -webkit-box; 
  display: -ms-flexbox; 
  display: flex; 
  margin-right: 6px;
  position: absolute;
  border-radius: 4px 0 0 4px;
}
.a_overflow { 
  background-color: #d9a5ef;
}
.b_secure { 
  background-color: #b1d6b1;
}
.c_observation { 
  background-color: #bbcaf4;
}
.d_adjustment { 
  background-color: #ffffa5;
}
.e_control { 
  background-color: #ffdfa5;
}
.f_off { 
  background-color: #e8a5a5;
}
.g_none { 
  background-color: #eee;
}
@media (min-width: 600px) {
.list-group-item {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 48.25%;
            flex: 1 0 48.25%;
    max-width: 48.25%;
}
}
@media (min-width: 1200px) {
.list-group-item {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 32.5%;
            flex: 1 0 32.5%;
    max-width: 32.5%;
}
}



