
#legend {
  margin: 0 auto;
  padding: 0 15px 28px 15px;
  max-width: 1600px;
  width: 90%;
  color: #888;
}
#legend span {
  margin-right: 12px;
  display: block;
}
h4 + .title {
  margin-top: 0;
}
#legend div{
  margin-right: 2px;
  margin-bottom: 2px;
  display: inline-block;
  vertical-align: middle;
}
#legend .title {
  display: block;
  font-size: 1.1em;
  margin-bottom: 4px;
  margin-top:8px;
}
#legend .overflow,
#legend .secure,
#legend .observation,
#legend .adjustment,
#legend .control,
#legend .off,
#legend .none {
  border: 1px solid #ddd;
  height: 22px;
  width: 10px;
}
#legend .overflow { 
  background-color: #d9a5ef;
}
#legend .secure { 
  background-color: #b1d6b1;
}
#legend .observation { 
  background-color: #bbcaf4;
}
#legend .adjustment { 
  background-color: #ffffa5;
}
#legend .control { 
  background-color: #ffdfa5;
}
#legend .off { 
  background-color: #e8a5a5;
}
#legend .none { 
  background-color: #eee;
}
#legend .glyphicon {
  height: 16px;
  line-height: 8px;
}
.footer {
  max-width: 1600px;
  margin: 0 auto;
  padding-bottom: 28px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 90%;
}
@media (min-width: 600px) {
#legend span {
    display: inline-block;
}
}




